<template>
  <div class="contactus">
    <!-- NAVBAR -->
    <navbar></navbar>
    <a href="https://www.google.com/maps/place/De+Franco+Dental/@32.7160919,-114.7297797,15z/data=!4m5!3m4!1s0x0:0x3f46dbd67b2624da!8m2!3d32.7160919!4d-114.7297797">
      <img id="mapDFranco" src="../assets/mapfranco.jpg" alt="mapDeFrancoDental">
    </a>
    <!-- TABLE INFORMATION DENTAL -->
    <div style="background-color: #6F51C7;">
      <br>
    </div>
    <br>
    <!-- TITLE CONTACT US FOR A CONSULTATION -->
    <h3>
      <b>CONTACT US FOR A CONSULTATION</b>
    </h3>
    <br> <br>
    <!-- FORM CONTACT US FOR SERVICES -->
    <b-container class="consultation-input">
      <b-form @submit.stop.prevent="onSubmit">
        <b-row>
            <b-col>
                <!-- Input Name -->
                <b-form-group id="example-input-group-1" label-for="example-input-1">
                    <b-form-input
                    id="example-input-1"
                    name="example-input-1"
                    v-model="$v.form.name.$model"
                    :state="validateState('name')"
                    aria-describedby="input-1-live-feedback"
                    placeholder="Enter your name"
                    ></b-form-input>

                    <b-form-invalid-feedback
                    id="input-1-live-feedback"
                    >This is a required field and must be at least 3 characters.</b-form-invalid-feedback>
                </b-form-group>

              <!-- Input Email -->
                <b-form-group id="example-input-group-1" label-for="example-input-1">
                    <b-form-input
                    id="example-input-1"
                    name="example-input-1"
                    v-model="$v.form.email.$model"
                    :state="validateState('email')"
                    aria-describedby="input-1-live-feedback"
                    type="email"
                    placeholder="Enter your email"
                    ></b-form-input>

                    <b-form-invalid-feedback id="input-1-live-feedback"> This is a required field. </b-form-invalid-feedback>
                </b-form-group>
                
                <!-- Input Message -->
                <b-form-group id="example-input-group-1" label-for="example-input-1">
                    <b-form-textarea
                    id="example-input-1"
                    name="example-input-1"
                    v-model="$v.form.message.$model"
                    :state="validateState('message')"
                    aria-describedby="input-1-live-feedback"
                    rows="3"
                    max-rows="6"
                    placeholder="Enter your message"
                    ></b-form-textarea>

                    <b-form-invalid-feedback id="input-1-live-feedback"> This is a required field. </b-form-invalid-feedback>
                </b-form-group>
            </b-col>

            <b-col>
                    <!-- Input Last Name -->
                <b-form-group id="example-input-group-1" label-for="example-input-1">
                    <b-form-input
                    id="example-input-1"
                    name="example-input-1"
                    v-model="$v.form.lastname.$model"
                    :state="validateState('lastname')"
                    aria-describedby="input-1-live-feedback"
                    placeholder="Enter your last name"
                    ></b-form-input>

                    <b-form-invalid-feedback
                    id="input-1-live-feedback"
                    >This is a required field and must be at least 3 characters.</b-form-invalid-feedback>
                </b-form-group>

                    <!-- Input Phone  -->
                <b-form-group id="example-input-group-1" label-for="example-input-1">
                    <b-form-input
                    id="example-input-1"
                    name="example-input-1"
