                    placeholder="Enter your phone"
                    ></b-form-input>

                    <b-form-invalid-feedback
                    id="input-1-live-feedback"
                    >This is a required field and must be at least 10 characters.</b-form-invalid-feedback>
                </b-form-group>

                


            <!-- Service Input -->
            <b-form-group id="example-input-group-2" label-for="example-input-2">
                <b-form-select
                id="example-input-2"
                name="example-input-2"
                v-model="$v.form.service.$model"
                :options="services"
                :state="validateState('service')"
                aria-describedby="input-2-live-feedback"
                ></b-form-select>

                <b-form-invalid-feedback id="input-2-live-feedback">This is a required field.</b-form-invalid-feedback>
            </b-form-group>

            <b-button pill block v-b-modal.modal-1 class="btn-send" type="submit" variant="primary" @click="addtoAPI">Submit</b-button>
            <b-modal id="modal-1" title="Hey Hello!">
              <p id="text-modal"><img id="tooth-modal" src="../assets/010-approve.png" alt="">Your information has been sent, we will respond as soon as possible.</p>
            </b-modal>
          <modalfalse/>
            </b-col>
        </b-row>
    </b-form>
    </b-container>
  </div>
