<script>
import navbar from '../components/navbar'
/*import modalfalse from '../components/ModalFalse'*/
import { validationMixin } from "vuelidate";
import { required, minLength } from "vuelidate/lib/validators";



export default {
  mixins: [validationMixin],
  components: {
    navbar,
    /*modalfalse*/
  },
  data() {
      return {
          services: [
            { value: null, text: 'I’m interested in service' },
            { value: 'Dental Implants', text: 'Dental Implants' },
            { value: 'Braces', text: 'Braces' },
            { value: 'Crowns and Bridges', text: 'Crowns and Bridges' },
            { value: 'Endodontics', text: 'Endodontics' },
            { value: 'Fillings and Extractions', text: 'Fillings and Extractions' }
          ],
          form: {
            name: null,
            service: null,
            email: null,
            lastname: null,
            phone: null,
            message: null
          }
        }
    },
    validations: {
    form: {
      service: {
        required
      },
      name: {
        required,
        minLength: minLength(3)
      },
      email: {
        required,
      },
      lastname: {
        required,
      },
      phone: {
        required,
        minLength: minLength(10)
      },
      message:{
        required
      },
    }
  },
    methods: {
      addtoAPI(){
        this.axios.post('http://localhost:3000/email',{

            name:this.name,
            email:this.email,
            text:this.message,
            ln:this.lastname,
            phone:this.phone,
            service: this.selected

          })
        .then(res=> {
          console.log('Acción realizada con éxito'+res.data);
        })
        .catch((error) => {
          console.log('[es este error!!!]'+error.response);
        })
      },
          validateState(name) {
      const { $dirty, $error } = this.$v.form[name];
      return $dirty ? !$error : null;
    },
    onSubmit() {
      this.$v.form.$touch();
      if (this.$v.form.$anyError) {
        return;
      }

      alert("Form submitted!");
    }
    }
}
</script>

